<template>
<div class="index">
  <div class="wrap1">
    <header>
      <div class="hearder-item" @click="search()">
        西安外事
      </div>
    </header>
    <div class="search" v-bind:class="{ searchfixed: isfixed}">
      <router-link to="/search" class="search-wrap">
          <i class="fas fa-search"></i>
          <span>搜索商家、商品名称</span>
      </router-link>
    </div>
    <div class="carousel">
      <mt-swipe>
        <mt-swipe-item>
          <router-link to="/red">
             <img src="static/elm/img/carousel/2.png" alt="" class="car-img">
          </router-link>
        </mt-swipe-item>
        <mt-swipe-item>
          <router-link to="/newuser">
            <img src="static/elm/img/carousel/1.png" alt="" class="car-img">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="menu">
      <mt-swipe :auto="0" :show-indicators="false">
        <mt-swipe-item>
          <router-link to="/msite/food" class="menu-item" v-for="n in menu1" :key="n.id">
            <img :src="n.src" alt="">
            <span>{{n.title}}</span>
          </router-link>
        </mt-swipe-item>
        <mt-swipe-item>
          <router-link to="" class="menu-item" v-for="n in menu2" :key="n.id">
            <img :src="n.src" alt="">
            <span>{{n.title}}</span>
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="coupon-wrap">
      <section class="coupont">
        <router-link to="/newuser">
            <img src="static/elm/img/new.png" alt="">
        </router-link>
      </section>
      <section class="couponb">
        <div class="couponb-wrap">
          <h3>限量抢购</h3>
          <div>超值美味免费送</div>
          <div><span>999</span>人在抢</div>
          <img src="static/elm/img/coupon/1.png" alt="">
        </div>
        <div class="couponb-wrap">
          <h3>限量抢购</h3>
          <div>超值美味免费送</div>
          <div>立即抢购</div>
          <img src="static/elm/img/coupon/2.png" alt="">
        </div>
      </section>
    </div>
    <div class="shoplist-title">
       推荐商家
    </div>
    <shop-list :list='msiteList'></shop-list>
    <index-footer></index-footer>
  </div>
  <div class="wrap-search" v-if="show">
    <header class="s-header">
      <span class="back" @click="searchBack()">
        <i class="fas fa-chevron-left"></i>
      </span>
      <span>选择收货地址</span>
    </header>
  </div>
</div>
</template>
<script>

import { mapState } from 'vuex'
const indexFooter = () => import('@/components/footer/index-footer')
const shopList = () => import('@/components/shop-list')
export default {
  components: {
    indexFooter,
    shopList
  },
  data: function () {
    return {
      show: false,
      isfixed: false,
      menu1: [
        {
          src: 'static/elm/img/menu/1.png',
          title: '美食'
        },
        {
          src: 'static/elm/img/menu/2.png',
          title: '晚餐'
        },
        {
          src: 'static/elm/img/menu/3.png',
          title: '商超'
        },
        {
          src: 'static/elm/img/menu/4.png',
          title: '果蔬'
        },
        {
          src: 'static/elm/img/menu/5.png',
          title: '医药'
        },
        {
          src: 'static/elm/img/menu/6.png',
          title: '大牌'
        },
        {
          src: 'static/elm/img/menu/7.png',
          title: '鲜花'
        },
        {
          src: 'static/elm/img/menu/8.png',
          title: '麻辣烫'
        },
        {
          src: 'static/elm/img/menu/9.png',
          title: '地方菜系'
        },
        {
          src: 'static/elm/img/menu/10.png',
          title: '披萨'
        }
      ],
      menu2: [
        {
          src: 'static/elm/img/menu/10.png',
          title: '地方小吃'
        }
      ]
    }
  },
  computed: {
    ...mapState([
      'msiteList'
    ])
  },
  created(){
    this.$store.dispatch('getMsite')
  },
  mounted() {
    window.onscroll = scroll;
    let a=this;
    function scroll(){
      if (window.scrollY >= 62){
          a.isfixed = true;
      }
      else if(window.scrollY < 62){
          a.isfixed = false;
      }
    }
  },
  methods: {
    search: function () {
      this.show = true
    },
    searchBack: function () {
      this.show = false
    }
  }
}
</script>
<style scoped>
/*搜索wrap*/
.wrap-search{
  width: 100vw;
  height: 100vh;
  z-index: 2;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: #f4f4f4;
  text-align: center;
}
.s-header{
  line-height: 11vw;
  font-size: 5vw;
  color: rgb(255, 255, 255);
}
.back{
  float: left;
}
/*首页定位*/
header{
  width: 100vw;
  height: 15vw;
  padding: 2vw 5vw;
  background-image: linear-gradient(90deg,#0af,#0085ff);
}
.hearder-item{
  padding: 2.5vw 0;
  font-size: 4.5vw;
  line-height: 6vw;
  color: rgb(255, 255, 255);
}
/*搜索框*/
.search{
  width: 100vw;
  height: 12vw;
  padding: 1vw 5vw;
  background-image: linear-gradient(90deg,#0af,#0085ff);
  z-index: 2;
}
.search-wrap{
  display: block;
  height: 10vw;
  width: 90vw;
  background-color: rgb(255, 255, 255);
  line-height: 10vw;
  color: rgb(147, 147, 148);
  text-align: center;
  font-size: 4vw;
  font-weight: 300;
}
.searchfixed{
  position: fixed;
  top: 0;
  right: 0;
}
/*轮播*/
.carousel{
  height: 28vw;
}
.car-img{
  width: 100%;
}
/*菜单*/
.menu{
  height: 40vw;
  width: 100%;
}
.menu-item{
  height: 20vw;
  width: 20vw;
  float: left;
  background-color: rgb(255, 255, 255);
  text-align: center;
}
.menu-item img{
  height: 12vw;
  width: 12vw;
  display: block;
  margin: 0 auto;
}
.menu-item span{
  height: 8vw;
  font-size: 3.5vw;
  line-height: 8vw;
  font-weight: 300;
  color: rgb(36, 36, 36);
}
/*新用户*/
.coupon-wrap{
  width: 100vw;
  padding: 2.5vw 2.5vw;
}
.coupont {
  display: block;
  width: 100%;
}
.coupont img{
  display: block;
  width: 100%;
}
.couponb{
  height: 40vw;
}
.couponb-wrap{
  width: 46vw;
  float: left;
  height: 40vw;
  background-color: rgb(246, 247, 248);
  padding: 3.2vw 0 0 4vw;
  position: relative;
}
.couponb-wrap:first-child{
  margin-right: 2vw;
}
.couponb-wrap h3{
  color: rgb(216, 84, 84);
  font-size: 5vw;
  margin: 3vw 0;
}
.couponb-wrap div{
  color: rgb(88, 88, 88);
  font-size: 3.5vw;
  line-height: 6vw;
  height: 6vw;
}
.couponb-wrap img{
  width: 30vw;
  position: absolute;
  right: 0;
  bottom: -2vw;
}
.shoplist-title{
  width: 100vw;
  height: 10vw;
  background-color: white;
  display: flex;
  -moz-box-align: center;
  -webkit-box-align: center;
  align-items: center;
  justify-content:center;
  font-size: 4vw;
}
.shoplist-title::before{
  display: block;
  content: '';
  margin-right: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
.shoplist-title::after{
  display: block;
  content: '';
  margin-left: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
.shopitem{
  width: 20vw;
  height: 100vw;
}
</style>



